<template>
  <ColorPicker
    v-model="isShowColorPicker"
    type="linear"
    @changeColor="changeColor"
    @onClose="onClosePicker"
    :pDeg="90"
    :pColor="pColor"
    :pColors="pColors"
    :showClose="isShowClose"
    :closeOnClickBody="false"
    :titleConfig="titleConfig"
  />
</template>

<script>
import ColorPicker from 'vue2-color-picker-gradient'

export default {
    name:"fdddf-colorpicker",
    components: {
      ColorPicker
    },
    props:{
      value:{
        type:String,
        default:"#000000" //, {hex: '#000000',rgba: { r: 0, g: 0, b: 0, a: 1 },color: 'rgba(0,0,0,1)'}
      },
      isShowColorPicker: {
        type:Boolean,
        default:true //
      },
      title:{
        type:String,
        default:"" //
      },
      isShowClose:{
        type:Boolean,
        default:true //
      },
      isShowTitle:{
        type:Boolean,
        default:true //
      }
    },
    data() {
      return {
        style: '',
        titleConfig: {
          show: this.isShowTitle,//  
          text: this.title//
        },
        pDeg: 60,
        pColor: {
          hex: '#194d33',
          hex8: '#194d33',
          hsl: { h: 150, s: 0.5, l: 0.2, a: 1 },
          hsv: { h: 150, s: 0.66, v: 0.3, a: 1 },
          rgba: { r: 25, g: 77, b: 51, a: 1 },
          a: 1,
          color: this.value,
        },
        pColors: [
          {
            color: 'rgba(255, 255, 255, 1)',
            hex: '#ffffff',
            rgba: { r: 255, g: 255, b: 255, a: 1 },
            pst: 100,
          },
          {
            color: 'rgba(0, 0, 0, 1)',
            hex: '#000000',
            rgba: { r: 0, g: 0, b: 0, a: 1 },
            pst: 0,
          },
        ],
      }
    },
    mounted() {
      this.run()
    },
    methods: {
      async run() {
      },
      changeColor({ style, colors, deg, color }) {
        console.log(style, colors, deg, color)

        this.$emit('change', { color })
        this.$emit("update:value", color.hex);
      },
      showPicker() {
        this.isShowColorPicker = true
      },
      onClosePicker() {
        this.isShowColorPicker = false
        this.$emit('close', {})
      },
    },
}
</script>

